
































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Head from '@/components/Head.vue';

@Component({
  components: {Head}
})
export default class Detail extends Vue {
  number = 0;
  goodsList: Item[] = [];
  barcode = '';
  asknum = '';
  stocknum = '';

  get goodsname() {
    const x: any[] = [];
    this.goodsList.map(item => {
      if (item.barcode === this.barcode) {
        x.push(item.goodsname);
      }
    });
    return x[0];
  }

  get spec() {
    const x: any[] = [];
    this.goodsList.map(item => {
      if (item.barcode === this.barcode) {
        x.push(item.spec);
      }
    });
    return x[0];
  }

  created() {
    const queryObj = this.$route.query as { barcode: string; asknum: string; stocknum: string };
    this.barcode = queryObj.barcode;
    this.asknum = queryObj.asknum;
    this.stocknum = queryObj.stocknum;
    this.$store.commit('getUser');
    const value = {
      creater: this.$store.state.user.userno,
      barcode: this.barcode
    };
    this.$store.dispatch(
      'getDetail',
      {
        url: '/sx/order/goods/ordermx',
        method: 'POST',
        value: JSON.stringify(value)
      }).then(res => {
      console.log(res);
      this.goodsList = res.data.resultObj.map((item: ItemNameDetail) => item.carddata);
    });
  }
}
